const { BlobServiceClient } = require("@azure/storage-blob");

const blobServiceClient = new BlobServiceClient("https://dghwarehousestrapi.blob.core.windows.net/?sv=2020-08-04&ss=bfqt&srt=sco&sp=rwdlacupitfx&se=2022-03-20T20:02:37Z&st=2022-03-20T12:02:37Z&spr=https&sig=PBSpZINPdVZo%2FFLcEBFLtHQzz2rcmtEwQoXPiFdHi6c%3D");
console.log(blobServiceClient);
blobServiceClient.setProperties({
    defaultServiceVersion: "2019-07-07"
});
const containerClient = blobServiceClient.getContainerClient("dgh-warehouse-strapi");
const blockBlobClient = containerClient.getBlockBlobClient("1-Back To School.m4v");
blockBlobClient.download().then(res => {    
    // return res.blobBody;
    document.getElementById("player").src = res.blobBody;
});
